import React, {useState, useEffect} from 'react';

import {makeStyles} from '@material-ui/core/styles';

import {Card, CardHeader, Divider, Grid, OutlinedInput, InputAdornment} from '@material-ui/core';

import {gridSpacing} from '../../store/constant';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import {IconSearch} from '@tabler/icons';

import axios from 'axios';
import {ShowSelectedPayment} from './ShowSelectedPayment';

const useStyles = makeStyles({
    table: {
        minWidth: 350
    },
    searchControl: {
        width: '100%',
        paddingRight: '8px',
        paddingLeft: '16px',
        marginBottom: '16px',
        marginTop: '16px'
    }
});

export default function Payments() {
    const classes = useStyles();
    const BASE_URL = 'https://backend.alsahaba.sa/api';
    const [payments, setPayments] = React.useState([]);
    const [value, setValue] = React.useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [selectedPayment, setSelectedPayment] = React.useState([]);
    const [selectedStatus, setSelectedStatus] = useState('');

    const itemsPerPage = 10;
    const [visible, setVisibility] = React.useState(false);

    const fetchPayments = async () => {
        await axios.get(`${BASE_URL}/payments`).then((response) => {
            setPayments(response.data);
        });
    };
    const fetchSearch = async () => {
        await axios.get(`${BASE_URL}/payment-search?query=${value}`).then((response) => {
            setPayments(response.data);
            setCurrentPage(1);
        });
    };

    const handleChange = (event) => {
        if (event.target.value === '') {
            setValue(event.target.value);
            fetchPayments();
        } else {
            setValue(event.target.value);
            fetchSearch();
        }
    };
    const showSelectedPayment = (payment) => {
        setVisibility(true);
        setSelectedPayment(payment);
    };
    const closePopup = () => {
        setVisibility(false);
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };

    useEffect(() => {
        fetchPayments();
    }, []);
    const startIndex = (currentPage - 1) * itemsPerPage;
    const endIndex = startIndex + itemsPerPage;
    const displayedPayments = payments
        .filter((payment) => !selectedStatus || payment.status === selectedStatus)
        .slice(startIndex, endIndex);
    const isLastPage = currentPage * itemsPerPage >= payments.length;

    const handleShowPrevious = () => {
        setCurrentPage((prevPage) => Math.max(prevPage - 1, 1));
    };

    const handleShowMore = () => {
        setCurrentPage((prevPage) => prevPage + 1);
    };

    return (
        <React.Fragment>
            {visible && <ShowSelectedPayment payment={selectedPayment} handleClose={closePopup} />}
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card>
                        <Grid container direction="row" justifyContent="space-between" alignItems={'center'}>
                            <Grid item>
                                <CardHeader title="All Payments" />
                            </Grid>
                            <Grid item>
                                <select
                                    value={selectedStatus}
                                    className={classes.searchControl}
                                    onChange={(e) => setSelectedStatus(e.target.value)}
                                    style={{height: '40px'}}
                                >
                                    <option value="">All Status</option>
                                    <option value="CAPTURED">Success</option>
                                    <option value="FAILED">Failed</option>
                                </select>
                            </Grid>
                            <Grid item style={{marginRight: '25px'}}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-profile"
                                    value={value}
                                    onChange={handleChange}
                                    placeholder="Order ID"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1.3rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                    aria-describedby="search-helper-text"
                                    inputProps={{
                                        'aria-label': 'weight'
                                    }}
                                    labelWidth={0}
                                />
                            </Grid>
                        </Grid>
                        <Divider />
                        <TableContainer>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>#</TableCell>
                                        <TableCell>Order ID</TableCell>
                                        <TableCell align="left">Creation Time</TableCell>
                                        <TableCell align="left">Message</TableCell>
                                        <TableCell align="left">Status</TableCell>
                                        <TableCell align="left">Total Authorized Amount</TableCell>
                                        <TableCell align="left">Total Captured Amount</TableCell>
                                        <TableCell align="left">Payment Info</TableCell>
                                        <TableCell align="left">Consultation Status</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {displayedPayments.map((payment, index) => (
                                        <TableRow key={payment.id} onClick={() => showSelectedPayment(payment)}>
                                            <TableCell align="left">{index + 1}</TableCell>
                                            <TableCell component="th">{payment.order_id}</TableCell>
                                            <TableCell align="left">{formatDate(payment.creation_time)}</TableCell>
                                            <TableCell align="left">{payment.message}</TableCell>
                                            <TableCell align="left">{payment.status}</TableCell>
                                            <TableCell align="left">{payment.total_authorized_amount}</TableCell>
                                            <TableCell align="left">{payment.total_captured_amount}</TableCell>
                                            <TableCell align="left">
                                                <div>
                                                    {payment.payment_mechanism}
                                                    {': '} {payment.payment_info} {payment.expiry_month}
                                                    {'/'}
                                                    {payment.expiry_year}
                                                </div>
                                            </TableCell>
                                            <TableCell align="left">{payment.payment_status ? payment.payment_status : 'none'}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                        <Grid container justifyContent="center">
                            {' '}
                            {currentPage > 1 && (
                                <Button onClick={handleShowPrevious} variant="outlined">
                                    Show Previous Page
                                </Button>
                            )}
                            {!isLastPage && (
                                <Button onClick={handleShowMore} variant="outlined">
                                    Show More
                                </Button>
                            )}
                        </Grid>
                    </Card>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}
