import React, {useEffect, useState} from 'react';
import {Container, Modal} from 'react-bootstrap';
import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import MuiTypography from '@material-ui/core/Typography';
import SubCard from '../../ui-component/cards/SubCard';
import {ShowConsultationCallID} from './ShowConsultationCallID';

export const ShowSelectedPayment = (props) => {
    const [showConsultation, setShowConsultation] = React.useState(false);
    const [selectedConsultation, setSelectedConsultation] = useState([]);
    const [showModal, setShowModal] = useState(true);

    const handleCloseConsultation = () => {
        setShowModal(false);
        // Other logic as needed
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const closeConsultationPopup = () => {
        setShowConsultation(false);
    };

    const showSelectedConsultation = (call_id) => {
        closeConsultationPopup(); // Close the previous modal before showing the new one
        setSelectedConsultation(call_id);
        setShowConsultation(true);
    };

    const closeConsultationPopUp = () => {
        setShowConsultation(false);
    };

    return (
        <>
            <Container>
                {showConsultation && (
                    <ShowConsultationCallID
                        consultation={selectedConsultation}
                        showModal={showModal}
                        handleCloseConsultation={closeConsultationPopup}
                    />
                )}
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Card className="popup-container">
                            <CardHeader title="Payment Info" />
                            <Divider />
                            <CardContent className="popup-body" id="popupCustom">
                                <Button id="closeBtn" onClick={() => props.handleClose()}>
                                    X
                                </Button>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sm={6}>
                                        <SubCard title={props.payment.order_id}>
                                            <Grid container direction="column" id="poppins" spacing={1}>
                                                <Grid item>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Order ID: &nbsp;
                                                        <span id="small-text">{props.payment.order_id}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Creation Time: &nbsp;
                                                        <span id="small-text">{formatDate(props.payment.creation_time)}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Message: &nbsp;
                                                        <span id="small-text">{props.payment.message}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Status: &nbsp;
                                                        <span id="small-text">{props.payment.status}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Total Authorized Amount: &nbsp;
                                                        <span id="small-text">{props.payment.total_authorized_amount}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Total Cuthorized Amount: &nbsp;
                                                        <span id="small-text">{props.payment.total_captured_amount}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Payment Info: &nbsp;
                                                        <span id="small-text">{props.payment.payment_info}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Payment Mechanism: &nbsp;
                                                        <span id="small-text">{props.payment.payment_mechanism}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Brand: &nbsp;
                                                        <span id="small-text">{props.payment.brand}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Scheme: &nbsp;
                                                        <span id="small-text">{props.payment.scheme}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Expiry Month: &nbsp;
                                                        <span id="small-text">{props.payment.expiry_month}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Expiry Month: &nbsp;
                                                        <span id="small-text">{props.payment.expiry_year}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Consultation Status: &nbsp;
                                                        <span id="small-text">{props.payment.payment_status}</span>
                                                    </MuiTypography>
                                                    {props?.payment?.call_id && (
                                                        <Button
                                                            variant="contained"
                                                            color="primary"
                                                            onClick={() => showSelectedConsultation(props?.payment.call_id)}
                                                            disableElevation
                                                            type="submit"
                                                        >
                                                            Show Consultation
                                                        </Button>
                                                    )}
                                                </Grid>
                                            </Grid>
                                        </SubCard>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};
