import React, {useEffect, useState} from 'react';
import {Card, CardHeader, Divider, CardContent, Grid, Button, Modal} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import {Container, ListGroup} from 'react-bootstrap';
import SubCard from '../../ui-component/cards/SubCard';
import axios from 'axios';

export const ShowConsultationCallID = (props) => {
    const BASE_URL = 'https://backend.alsahaba.sa/api';
    const [consultations, setConsultations] = useState([]);

    const fetchConsultations = async () => {
        await axios.get(`${BASE_URL}/consultations/`).then((response) => {
            setConsultations(response.data);
        });
    };

    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    useEffect(() => {
        fetchConsultations();
    }, []);

    return (
        <>
            <Modal
                open={props.showModal}
                onClose={props.handleModalClose}
                aria-labelledby="total-income-pattern-modal-title"
                aria-describedby="total-income-pattern-modal-description"
            >
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Card className="popup-container">
                            <CardContent className="popup-body" style={{top: '250px', overflowY: 'auto', height: '75vh'}}>
                                <Button id="closeBtn" onClick={props?.handleCloseConsultation}>
                                    X
                                </Button>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sm={6}>
                                        <>
                                            {consultations &&
                                                consultations
                                                    ?.filter((consultation) => consultation.id === props?.consultation)
                                                    .map((filteredConsultation, index) => (
                                                        <SubCard title="Consultation">
                                                            <div className="mt-4">
                                                                <h5>Chief Complaint:</h5>
                                                                <strong>{filteredConsultation.chief_complaint}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>History of Illness:</h5>
                                                                <strong>{filteredConsultation.history_of_illness}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>Review of systems:</h5>
                                                                <strong>{filteredConsultation.review_of_systems}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>Examinations:</h5>
                                                                <strong>{filteredConsultation.examination}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>Assessments/Plans:</h5>
                                                                <strong>{filteredConsultation.assessment}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>Medication:</h5>
                                                                <strong>{filteredConsultation.medication}</strong>
                                                            </div>
                                                            <div className="mt-4">
                                                                <h5>Sick leave:</h5>
                                                                <strong>{filteredConsultation.sick_leave}</strong>
                                                            </div>
                                                            <div className="mt-4 mb-4">
                                                                <h5>Created At:</h5>
                                                                <strong>{formatDate(filteredConsultation.created_at)}</strong>
                                                            </div>
                                                        </SubCard>
                                                    ))}
                                        </>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Modal>
        </>
    );
};
